
.chat-btn{
    border-radius: $radius;
    font-size: 15px;
    position: relative;
    cursor: pointer;  
   display: inline-flex;
    padding: 9px 14px;
    border: 1px solid transparent;
    text-align: center;
    vertical-align: middle;
}
.msg_send_btn {
    background: #0a80ff none;
    border-color: #0a80ff;
    color: #fff;   
    margin-right: 5px;
}
.btn-light{
    background:darken($off-white,10%); 
    border-color: darken($off-white,5%);    
}

.tooltiptext{
    visibility: hidden;
    width: 70px;
    background-color: $grey;
    color: #fff;
    font-size:12px;    
    text-align: center;
    border-radius: $radius;
    padding: 5px 0;
    position: absolute;
    z-index: 1;    
    transition: opacity 0.3s;
}
.btn_signout_tooltip{
    left: 100%;
    top: 130%;
    margin-left: -60px; 
}
.footer_tooltip{
    left: 100%;
   bottom: 110%;
    margin-left: -60px; 
}

.btn-light:hover .tooltiptext{
    visibility: visible;  
    opacity: 1;
}

.emojiPicker{
    position: absolute;
    bottom: 80px;
    float: left;
  }
