.content{  
    display: inline-block;   
    width: calc(100% - 10px);
    margin-bottom: 0px;
  }
.send{
    float:right;       
    clear: both; 
    margin-top: -5px;
    .message{
        margin-bottom: 10px;
    }
    .time{
        float: right;
    }
   
}
.receive{
    float: left;  
    clear: both; 
    margin-left: $xs-size;
    margin-top: 5px;    
    .message{
        margin-bottom: 10px;
    }
    .time{
        float: left;
    }
}
.time{
    margin-bottom: 0;
    font-size:13px;
    color:#05728f;
    
}
.message{ 
    color: $grey;
    max-width:500px;
    word-break: break-all;  
    display: block; 
   .highlight{
    background: darken($off-white ,5%);  
    padding:5px 10px; 
    display:inline-block;
    border-radius: $radius;
   
    }
    
}

