.main{    
    color: $dark-grey;  
    background: hsl(0, 0, 100);
    border: 1px solid darken($off-white,10%);  
    box-shadow: 0 3px 25px hsla(0, 0, 0, 0.2);
    border-radius: $radius-10px;
    display: flex;      
     margin:5px 10px;     
     flex-direction: row;
     min-height: 100vh;  
  //  height: 100vh; 
    width: calc(100% - 10px);
   
}
.userContainer{
    //width: 20%;
    flex: 1;
    border-right: 1px solid darken($off-white,5%);     
}
.header{  
    border-bottom: 1px solid darken($off-white,5%);    
    display:flex;  
     flex-direction: row;
    padding: 17px 30px; 
    justify-content: space-between;  
}

.userList{
    background:$off-white;
}

.room-member{
    background: #fff; 
    border-radius: $radius; 
    display:inline-block; 
    width: 15rem;
    margin:10px;
    padding:$xs-size;    
}
.chat_main{
   // width: 80%;
   flex: 4;

}
.content {
    display: flex;
    flex-direction: row;
  }
.chat_box{
    flex: 4;
    min-height: 80vh;  
    font-size: 13px;   
   
 }
 .chat_footer {         
   border-top: 1px solid darken($off-white,5%);     
   padding:10px 20px 10px 5px;  
   display: flex; 
 } 
 .chat_footer .emoji_div  {
    padding: 0;
    margin: 0;
    display: flex;
    flex:0 1 auto;
    button{
        margin-right: 10px;
    }
 }

 .msg_form{  
    display: flex;
    justify-content: flex-end;
    flex:6 0 auto;
 
     button{
       
        margin-right: 10px;
         position: relative;
     }
     
 }
 .chat_footer input{
    
     margin-right: 10px;
 }
 .write_msg_input
 { 
     display: inline-flex;
     width: 100%;
     height: calc(1.5em + .75rem + 2px);
     padding: .375rem .75rem;
     font-weight: 400;
     line-height: 1.5;
     color: #495057;
     background-color: #fff;
     background-clip: padding-box;
     border: 1px solid #ced4da;
     border-radius: $radius;
 }
     