.btn {
  display: block;
  background-color: hsl(196, 100%, 43%);
  border-color:  hsl(196, 100%, 43%);  
  color: hsl(0, 0, 100);
  font-size: 15px;
  text-transform: uppercase;
  text-decoration: none;
  margin: 20px 0;
  padding: 15px 15px;
  border-radius: 5px;
  position: relative;
  width: 100%;

  &::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all .2s ease-in-out;
    box-shadow: inset 0 3px 0 hsla(0, 0, 0, 0), 0 3px 3px hsla(0, 0, 0, .2);
    border-radius: 5px;
  }
  
  &:hover::after {
    background: hsla(0, 0, 0, 0.1);
    box-shadow: inset 0 3px 0 hsla(0, 0, 0, 0.2);
  }
  
} 




/*--------------------
Form
--------------------*/


.input_txt {
        border: none;
        padding: 0;
        margin: 20px 0;
        position: relative;
      width: 100%;
      height: 48px;
      color: hsl(0, 0, 20);
      padding: 15px 40px 15px 15px;
      border-radius: 5px;
      font-size: 14px;
      outline: none!important;
      border: 1px solid hsla(0, 0, 0, 0.3);
      box-shadow: inset 0 1px 4px hsla(0, 0, 0, 0.2);
      vertical-align: top;
}

 

/*--------------------
Signup
--------------------*/
.signup {

justify-content: center;
  display: flex;
  align-items: center;
  margin-top: 3rem;
}


.signup-classic {
  background: hsl(0, 0, 100);
  border-radius: $radius-10px;
  box-shadow: 0 3px 25px hsla(0, 0, 0, 0.2);
  padding: 1rem 2rem;
  text-align: center;
  width: 25rem;
    
  h2 {
    font-size: 16px;
    font-weight: normal;
    margin: 15px 0px;
  
    text-shadow: 0 2px 3px hsla(0, 0, 0, 0.1);
  }
}